import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import Topnav from "../topNav/topnav";
import Sidebar from "../sidebar/sidebar";
import Addenquiry from "./addEnquiry";

//import { userActions } from '../_actions';

class Enquiry extends React.Component {
  constructor() {
    super();
    this.state = { userlist: [] };
    this.list = this.list.bind(this);
  }
  componentDidMount() {
    //  this.props.dispatch(userActions.getAll());
    fetch("https://api.randomuser.me/?results=50")
      .then(res => res.json())
      .then(res => {
        //console.log(res.results);
        this.setState({ userlist: res.results });
      });
  }

  handleDeleteUser(id) {
    //  return (e) => this.props.dispatch(userActions.delete(id));
  }
  list() {
    return this.state.userlist.map((res, index) => {
      console.log(res);
      return (
        <tr key={index}>
          <td>{res.cell}</td>
          <td>{res.email}</td>
          <td>{res.gender}</td>
          <td>{res.id.value}</td>
          <td>{res.name.first}</td>
          <td>{res.cell}</td>
          <td>{res.cell}</td>

          <td>
            <button
              data-toggle="modal"
              data-target="#view-modal"
              ng-click="showModel(<?php  echo $row['id']; ?>)"
              data-id="<?php  echo $row['id']; ?>"
              id="getUser"
              className="btn btn-sm btn-info"
            >
              <i className="glyphicon glyphicon-eye-open" /> View
            </button>
            <a className="btn btn-danger btn-sm">
              <i className="fa fa-trash-o" />
            </a>
          </td>
        </tr>
      );
    });
  }

  render() {
    const { user, users } = this.props;
    return (
      <div className="main_container">
        <div className="col-md-3 left_col">
          <Sidebar />
        </div>
        <div className="top_nav">
          <div className="nav_menu">
            <Topnav />
          </div>
        </div>
        <div className="right_col" role="main">
          <div className="">
            <div className="page-title">
              <div className="title_left">
                <h3>
                  Users <small>List of all employees</small>
                </h3>
              </div>
              <div className="title_right">
                <div className="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                  <div className="input-group">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Search for..."
                    />
                    <span className="input-group-btn">
                      <button className="btn btn-default" type="button">
                        Go!
                      </button>
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="clearfix" />
            <div className="row">
              <div className="col-md-12 col-sm-12 col-xs-12">
                <div className="x_panel">
                  <button
                    type="button"
                    className="btn btn-primary"
                    data-toggle="modal"
                    data-target=".bs-example-modal-lg"
                  >
                    +
                  </button>
                  <div className="x_content">
                    <div
                      className="modal fade bs-example-modal-lg"
                      role="dialog"
                      ariaidden="true"
                    >
                      <Addenquiry />
                    </div>

                    <table
                      id="datatable-buttons"
                      className="table table-striped table-bordered"
                    >
                      <thead>
                        <tr>
                          <th>ID</th>
                          <th>Name</th>
                          <th>Type</th>
                          <th>Role</th>
                          <th>UserName</th>
                          <th>Password</th>
                          <th>Status</th>
                          <th>view</th>
                        </tr>
                      </thead>
                      <tbody>{this.list()}</tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Enquiry;
//const connectedHomePage = connect(mapStateToProps)(HomePage);
//export { connectedHomePage as HomePage };



